    background-color: #98e3ce;
    height: 300px;
    width: 34%;
    position: absolute;
    border-radius: 5px;
    transform-style: preserve-3d;
    box-shadow: -1px 5px 30px -8px rgba(00,0,0,0.75);

    /* display: none; */
}
.otherdetail::before{
