.login{
    /* border: 2px solid blue; */
    height: 100%;
    display: flex;
    
    background-color: #eafff9;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
button{
    padding: 7px;
    border-radius: 5px;
    background-color: #fff;
    border: none;
    margin: 5px;
    outline-width: 0;
}
.box,.sign_up{
    /* background-color: red; */
   
    width: 40vw;
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
   
}
#login_title{
    /* border: 2px solid yellow; */
    color: white;
    margin: 0;
    padding: 10px;
    
}
.logs{
    display: flex;
    height:70%;
    width: 100%;
    flex-direction: column;
    align-items: center;
    /* border: 2px solid blue; */
    margin-bottom: 0;
    margin-top: 0;
    padding:15px 0px;
    
    
}
.logs >input[type='text'],input[type='number']{
    width: 80%;
margin-bottom: 5%;
height: 40px;
background-color: transparent;
border: none;
outline-width: 0;
border-bottom: 1px solid white;
color: white;
padding-bottom: 5px;
font-size: 14pt;

}
#login_submit,#sign_submit,#after_go{
    width: 50%;
    margin-left: 0;
    max-width: 200px;
    cursor: pointer;

}
#after_go{
    width: 100%;
}
#s{
    display: none; 
}
.sign_up ::placeholder,#login_name::placeholder{
    color: white;  
    /* background-color: blue; */
}
.wrap{
    /* border: 5px solid green; */
    margin-top: -100px;
    display: flex;
    flex-direction: column;
    max-height: 80%;
    box-shadow: -1px 5px 30px -8px rgba(00,0,0,0.75);
    background-image: linear-gradient(to top right,#2A3166,#2A3166,#3b4379,#7a82b8,white);
   
}
#sign_up_show{
    background-color: transparent;
    color: white;
}
.hider{
    color: red;
    display: none;
}