.filebox{
    width: 700px;
    height: 400px;
    background-color: rgb(255, 224, 224);
    box-shadow: -1px 5px 30px -8px rgba(00,0,0,0.75);
    
  }
  .fille{
    /* border: 2px solid red; */
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: -1px 5px 30px -8px rgba(00,0,0,0.75);

  }

  #file_head{
    font-size: 30pt;
    text-align: center;
    /* border: 2px solid red; */
    padding-left: 30px;
  }
  #notes{
    text-align: center;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
    /* border: 2px solid red; */
  }
  #uploadbut{
    width: 50%;
  }
  #filelink{
    margin-top: 25px;
    /* border: 2px solid red; */
    color: white;
    padding: 10px;
    border-radius: 5px;
    background-color: #2A3166;
    min-width: none;
  }
  #copier{
    margin-top: 10px;
    /* border: 2px solid red; */
    color: white;
    padding: 10px;
    border-radius: 5px;
    background-color: #2A3166;
    min-width: none;
  }