.header{
    display: flex;
    padding: 10px;
    padding-right: 0;
    background-color: #2A3166;
    z-index: 5;
    color: white;
    max-width: 100%;
    height: 10vh;
    /* border: 2px solid red; */
}
.user_info{
    /* border: 2px solid red; */
    display: flex;
    width: 35%;
    height: 10vh;
    /* now we will add bg transition */
    background-image: linear-gradient(#EE7879,#EE7879);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: .4s;
    border-radius: 5px;
}
.user_info:hover{
    background-size: 100% 100%;
}
.user_info:hover .userdetail{
    background-color: black;
 }
 
.user_info::after{
    display: flex;
    margin-top: 2vh;
    margin-left: 10px;
    content: "";
    border-left: 2px solid white;  
    height: 60%;
}
.contact_info{
    /* border: 2px solid red; */
    display: flex;
    margin-right: 0;
    width: 35%;
    max-width: 70%;
    height: 10vh;
}
.contacts{
    /* border: 2px solid orange; */
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    background-image: linear-gradient(#98e3ce,#98e3ce);
    margin-right: 0;
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: .4s;
    padding-left: 20px;
    border-radius: 5px;
}
.contacts:hover{
    background-size: 100% 100%;
}

.logo{
    /* border: 2px solid blue; */
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.name{
    /* border: 2px solid green; */
    
    width: 50%;
    margin-right: 10%;
    
    /* text-shadow: 1px 1px 2px #EE7879; */
    padding-top: 0;
    
    
}
.name:hover{
    
    text-shadow: none;
    color: white;
    transition: 0.5s;
}
.contact_logo{
    /* border: 2px solid blue; */
    width: 25%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.contact_name{
    /* border: 2px solid green; */
    margin-left: 20px;
    width: 37%;
    height: 100%;
    
    padding-top: 0;
        
}
.contact_name:hover{
   
    
    text-shadow: none;
    color: white;
    cursor: pointer;
    transition: 0.5s ease-in;
}
h2{
    /* border: 2px solid brown; */
    text-align: center;
    height: 100%;
    margin: 0;
    margin-right: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
}
.logo,.contact_logo{
    display: flex;
    justify-content: center;
}
.header_buttons{
    width: 30%;
    /* border: 2px solid white; */
    display: flex;
    justify-content: flex-end;
    align-items: center;
   
}
.innerbut{
    /* border: 2px solid lightblue; */
    display: flex;
    justify-content: space-between;
    float: right;
    width: 50%;
    min-width: 200px;
    margin-right: 20px;
}
