.mainpeople{
    width: 100%;
    height: 100%;
    display: flex;
    z-index: 1;
    height: 87vh;
    position: absolute;
}
.people{
    /* border: 3px solid black; */
    width: 100%;
    text-align: center;
    overflow: scroll;
    max-height: 87vh;
    height: 100%;
    position: relative;
    
}
.searchname{
    width: 90%;
    height: 40px;
    outline-width: 0;
    background-color: white;
    padding-left: 5px;
    border-radius: 20px;
    border: none;
    margin-top: 10px;
    margin-bottom: 10px;
}


/* This is css for indivisual  */
.indivisual{
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    height: 75px;
    max-height: 75px;
    /* border: 2px solid blue; */
    display: flex;
    align-items: center;
    
}
.indivisual:hover{
    background-color: #98e3ce;
    transition: 0.2s ease-in;
}


